.pettycash-container {
  padding: 25px;
  border-radius: 10px;
  /* width: 500px; */
  margin: auto;
  height: 500px;

  /* background-color: rgba(255, 255, 255, 0.8); */
  box-sizing: border-box;
}

.Home {
  background-image: url(/static/media/kelly-sikkema-xoU52jUVUXA-unsplash.c2bbeaf5.jpg);
  background-position: center;
  background-origin: content-box;
  background-repeat: no-repeat;
  background-size: cover;
}

.add-bill-container {
  display: block;
  margin: auto;
  width: 350px;
}
.add-bill-form-control {
  display: block;
  width: 100%;
  padding: 5px;
  text-align: center;
  margin-bottom: 10px;
}

.bill-list-container {
  margin: auto;
  box-sizing: border-box;
  width: 300px;
}
.bill-list-row {
  display: block;
}
.bill-list-row-content {
  display: inline-block;
}
.edit-mode-btn {
  cursor: pointer;
}
.edit-mode-btn:hover {
  color: blue;
}

.bill-total-container {
  width: 350px;
  margin: auto;
  margin-top: 25px;
  margin-bottom: 5px;
  font-size: 24px;
}
.total-cost {
  font-weight: 600;
  margin-left: 10px;
}

.total-saved-container {
  width: 350px;
  margin: auto;
  margin-top: 5px;
  margin-bottom: 25px;
  font-size: 24px;
}
.total-saved {
  font-weight: 600;
  margin-left: 10px;
}

.interval-option-container {
  display: block;
  padding: 10px;
  margin: auto;
  text-align: center;
  margin-bottom: 25px;
}
.interval {
  font-weight: 400;
  display: inline-block;
  cursor: pointer;
  margin-right: 10px;
  margin-left: 10px;
  text-shadow: 1px 1px 1px gray;
}
.selected-interval {
  color: gold;
  text-shadow: 1px 1px 1px gray;
  font-weight: 700;
  display: inline-block;
  cursor: pointer;
  margin-right: 10px;
  margin-left: 10px;
}

.delete-button {
  display: inline-block;
  width: 100px;
  text-align: right;
  cursor: pointer;
}
.delete-button:hover {
  color: red;
}
.edit-mode-done-btn {
  cursor: pointer;
}
.edit-mode-done-btn:hover {
  color: blue;
}
.edit-bill-title {
  width: 100px;
  display: inline-block;
}
.edit-bill-cost-input {
  width: 200px;
  display: inline-block;
}

